<template>
    <div class="m-dashboard-work m-dashboard-cms" v-loading="loading">
        <div class="m-dashboard-work-header">
            <h2 class="u-title">剑三骚话</h2>
            <a :href="publishLink" class="u-publish el-button el-button--primary el-button--small">
                <i class="el-icon-document"></i> 说句骚话
            </a>
        </div>

        <el-input class="m-dashboard-work-search" placeholder="请输入搜索内容" v-model="search">
            <span slot="prepend">关键词</span>
            <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>

        <div class="m-dashboard-box">
            <ul class="m-dashboard-box-list" v-if="data && data.length">
                <li v-for="(item, i) in data" :key="i">
                    <el-tooltip
                        class="item"
                        effect="dark"
                        content="待审核"
                        placement="top-start"
                        v-if="!item.status"
                    >
                        <i class="u-item-icon u-icon-pending">
                            <svg data-name="图层 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 239.97 312" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M23.99 288v-22.83c.09-10.86-.25-21.77.52-32.59 1.27-17.85 9.59-32.59 21.91-45.23 11.37-11.67 25-20.28 39.18-28 1.84-1 3.7-2 6.24-3.34l-6.54-3.51c-15.43-8.45-30.08-17.9-41.92-31.15-13.03-14.61-19.66-31.59-19.39-51.27.17-13.87 0-27.75 0-41.62V24h-11c-7.63-.07-13-5.15-13-12.1S5.37.02 12.74.01h214.48c7.33 0 12.7 5.11 12.74 11.9s-5.29 12-12.57 12.08c-3.62.05-7.23 0-11.41 0v4.1c0 13.88-.16 27.75 0 41.62.29 20.58-7.12 38-21 52.92-12.3 13.28-27.41 22.72-43.27 31.1-1 .53-2 1-3 1.57a6.82 6.82 0 00-.86.77c1.26.73 2.35 1.43 3.5 2 15.9 8.33 31 17.77 43.41 31 14 15 21.55 32.46 21.25 53.19-.2 13.75 0 27.5 0 41.25v4.48h11c7.58.05 13 5.12 12.95 12.07 0 6.8-5.39 11.9-12.73 11.9H12.74c-7.33 0-12.7-5.11-12.75-11.9-.05-7 5.32-12 12.93-12.08 3.5 0 7 .02 11.07.02zm168-.17v-39.58a122.76 122.76 0 00-.4-13.84c-1.75-14.93-10.15-26-21.21-35.42-13.39-11.34-28.95-19.02-44.46-26.84-4.42-2.22-8.1-3-12.39-.07-2.54 1.72-5.54 2.75-8.38 4.15-13.31 6.66-26.29 13.87-37.49 23.86-13.07 11.67-20.41 25.84-19.75 43.83.42 11.35.08 22.74.08 34.11v9.8zm-144-263.64v4.31c0 13.12.38 26.25-.09 39.36-.66 18.25 6.75 32.64 20.14 44.36 14.83 13 32.38 21.48 50.07 29.68a5.12 5.12 0 003.78-.3c6.1-2.87 12.17-5.8 18.1-8.93 12.75-6.7 25-14.09 35.39-24.3 11-10.86 17-23.71 16.65-39.49-.34-13.49-.07-27-.07-40.49v-4.2z"/></svg>
                        </i>
                    </el-tooltip>
                    <i class="u-item-icon el-icon-picture-outline-round" v-else></i>
                    <a
                        class="u-title"
                        target="_blank"
                        :href="postLink(type, item.id)"
                    >{{ item.content || "未命名" }}</a>
                    <div class="u-desc">
                        <time class="u-desc-subitem">
                            <i class="el-icon-finished"></i>
                            发布 :
                            {{ item.created_at | dateFormat }}
                        </time>
                        <time class="u-desc-subitem">
                            <i class="el-icon-refresh"></i>
                            更新 :
                            {{ item.updated_at | dateFormat }}
                        </time>
                    </div>

                    <el-button-group class="u-action">
                        <el-tooltip class="item" effect="dark" content="编辑" placement="top-start">
                            <el-button size="mini" icon="el-icon-edit" @click="edit(type, item.id)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
                            <el-button size="mini" icon="el-icon-delete" @click="del(item.id,i)"></el-button>
                        </el-tooltip>
                    </el-button-group>
                </li>
            </ul>
            <el-alert
                v-else
                class="m-dashboard-box-null"
                title="没有找到相关条目"
                type="info"
                center
                show-icon
            ></el-alert>
            <el-pagination
                class="m-dashboard-box-pages"
                background
                :page-size="per"
                :hide-on-single-page="true"
                :current-page.sync="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
            ></el-pagination>
        </div>
    </div>
</template>

<script>
import { getMyJokes, deleteJoke } from "@/service/pvx.js";
import { editLink, getLink } from "@jx3box/jx3box-common/js/utils.js";
import dateFormat from "../utils/dateFormat";
export default {
    name: "joke",
    props: [],
    data: function () {
        return {
            loading: false,
            data: [],
            total: 1,
            page: 1,
            per: 10,
            search: "",
            type: "joke",
        };
    },
    computed: {
        params: function () {
            return {
                page: this.page,
                per: this.per,
                search: this.search,
            };
        },
        publishLink: function () {
            return "./#/" + this.type;
        },
    },
    watch: {
        params: {
            deep: true,
            immediate: true,
            handler: function (newval) {
                this.loadPosts();
            },
        },
    },
    methods: {
        loadPosts: function () {
            this.loading = true;
            getMyJokes(this.params)
                .then((res) => {
                    this.data = res.data.data.list;
                    this.total = res.data.data.total;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        edit: function (type, id) {
            location.href = "./#/" + type + "/" + id;
        },
        del: function (id, i) {
            this.$alert("确定要删除吗？", "确认信息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    if (action == "confirm") {
                        deleteJoke(id).then((res) => {
                            this.$notify({
                                title: "删除成功",
                                message: "骚话删除成功",
                                type: "success",
                            });
                            this.data.splice(i, 1);
                        });
                    }
                },
            });
        },
        postLink: function (type, id) {
            return getLink(type, id);
        },
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val));
        },
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/work.less";
</style>
